#tech_list {
    display: grid;
    grid-template-columns: repeat(2, minmax(140px, 200px));
    list-style-type: none;
    

}

#tech_list li::before {
    content: "\2023";
    color: var(--green);
    font-size: x-large;
    display: inline-block;
    width: 1em;
}

div#about_info {
    display: flex;
   
}

div#about_text {
    width: 65%;
    margin-right: 0px;
}

#portfolio_img_div {
    width: 35%;
    margin-left: 0;
    height: same-as-width;
    float: right;
    
    
}

img#portfolio_img {
    object-fit: contain;
    width: 100%;
   
    
}

.intro_paragraph {
    width: 80%;
    color: var(--text);
    
}

