
#education_content {
    display: flex;
    grid-template-columns: 25% 70%;
    
    column-gap: 5%;

}

div#buttons {
    width: 100%;
    min-width: 250px;
    max-width: 350px;
    
}

div.education_tab {
    width: 100%;
    min-width: 500px;
}

.education_button {
    text-align: left;
    padding-left: 25px;
    width: 100%;
    max-height:50px;
    display:inline-block;
    margin:0px;
    border: none;
    color: var(--text);
    font-size: 1rem;
    border-left: 2px solid var(--text);
    border-radius: 0px;
}

.education_button:hover {
    color: var(--green);
    background-color: var(--light-navy);
    
}
.selected {
    border-color: var(--green);
    color: var(--green);
    background-color: var(--light-navy);
}



div h3 {
    margin-top: 0px;
    color: var(--light-text);
    margin-bottom: 10px;
}

ul {
    color: var(--text);
    font-size: 1.5ch;
    line-height: 2.5ch;
}

ul#course_list {
    list-style-type: none;
}

@media screen and (max-width=600px) {
    #education_content {
        grid-template-columns: 50%
    }
    
}