div#content {
    min-height: 100vh;
    max-width: 1600px;
    margin: 0 auto;
    padding: 0 150px;
}

.contentBlock {
    width: 100%;
    max-width: 1000px;
    margin: 0px auto;
    padding-top: 200px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;
    
    padding-bottom: 100px;
}

@media (max-width: 1000px) {
    .contentBlock {
        
        max-width: 1600px;
        
    }

    #content {
        padding: 0 50px !important;
    }

}

div.contentBlock h2.header.after::after {
    content: "";
    display: block;
    margin-right: 0px;
    margin-left: 20px;
    top: -5px;
    width: 25%;
    height: 2px;
    background-color: var(--light-navy);
}

h2.header {
    width: 100%;
}

h2.center {
    text-align: center;
}
h2.block {
    display: block;
    
}

div#intro {
    color: var(--light-text);
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 0;
}
div#intro h1 {
    font-size: 5ch;
    
}



.green {
    color: var(--green);
}

div#footer {
    height:50px;
    margin-top: 150px;
    padding-bottom: 50px;
}

div#footer p {
    color: var(--text);
    font-size: 1.5ch;
    text-align: center;
    margin-bottom: 25px;
}

h2 {
    font-size: 2rem;
    color: var(--light-text);
    display: flex;
    align-items: center;
}

div#footer div#externals{
    display: flex;
    flex-wrap: wrap;
    position: relative;
    justify-content: center;
    text-justify: center;
}

div#externals a{
    width: 30px;
    height: auto;
    margin: 5px;
}



div#externals a svg {
    object-fit: contain;
    
    
    font-family: var(--font-sans);
    font-size: var(--fz-xl);
    line-height: 1;
    
    text-align: right;
    
    color: var(--light-text);

    stroke: var(--light-text);
    stroke-width: 1.5;
    stroke-linecap: round;
    stroke-linejoin: round;
    max-width: 100%;
    vertical-align: middle;
    fill: none;
    width: 25px;
    height: 25px;
}

div#externals a:hover svg {
    stroke: var(--green);
}

div#externals a.facebook_link {
    margin-left: 0px;
}

