main {
    padding: 0px;
}

div.header {
    height: 80px;
    display: inline-block;
    top: 0px;
    position: fixed;
    z-index: 10;
    width: 100vw;
    background-color: #0a192f;
    transition: 0.1s;
    padding-left:20px;
    
}

h1#title {
    color: white;
    width: 100%;
    padding-right: 0px;
}

div.titleContainer {
    width: 25%;
    float: left;
    margin-right: 0px;
    margin-top: 17.5px;
    margin-left: 3%;
}

div.quickScroll {
    margin-top: 30px;
    margin-right: 2.5%;
    float: right;
    
}

div.quickScroll a:not(.button) {
    font-size: 1.35ch;
    text-decoration: none;
    color: var(--light-text);
    padding: 25px;

}

div.quickScroll a.button {
    font-size: 1.35ch;
    color: var(--green);
    text-align: center;
    padding: 10px 15px;
    text-decoration: none;
    border-style: solid;
    border-radius: 5px; 
    border-width: 1px;
    margin-right: 50px;
}
  
div.quickScroll a.button:hover {
    background: var(--green-tint);
}

div.quickScroll a:not(.button):hover {
    color: var(--green);
}

div.titleContainer a svg {
    color: var(--light-text);

    stroke: var(--green);
    stroke-width: 1.5;
    stroke-linecap: round;
    stroke-linejoin: round;
    max-width: 100%;
    vertical-align: middle;
    fill: none;
    width: 52px;
    height: 52px;
}

div.titleContainer a svg:hover {
    fill: var(--green-tint);
}

@media (max-width: 600px) {
    div.titleContainer {
        width: 20%;
    }

    div.quickScroll {
        width: 70%;
    }
    div.quickScroll a {
       visibility: hidden;
    }

}


