div.contact {
    padding-bottom: 0px;

}

 p.contact_p {
    padding: 0px 25%;
    color: var(--text);
    line-height: 2.5ch;
    margin-bottom: 100px;
}

.button {
    align-self: center;
    color: var(--green);
    text-align: center;
    padding: 15px 25px;
    text-decoration: none;
    border-style: solid;
    border-radius: 5px; 
    border-width: 1.5px;
}

.button:hover {
    background: var(--green-tint);
}