div.feature_project {
    display: inline-block;
    height: 400px;
    width: 100%;
    margin-bottom: 5%;
    align-items: center;
}

div.project_image_div a.image_link {
    height: 100%;
    width: 100%;
    
    padding: auto;
    display: block;
    margin: 0;
    background: var(--green);
    filter: opacity(25%);
    
    
    
}



a.project_title_link h2:hover {
    color: var(--green);
}

div.project_image_div a:hover {
    background-color: none;
}


.project_img {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
    
}

div.project_image_div.left {
    left: 25%;
    margin-left: -25%;
}

div.project_image_div.right {
    right: 10%;
    margin-right: -25%;
}

div.project_image_div {
    width: 60%;
    height: 100%;
    position: relative;
    margin: 0;
    align-items: center;
    display: inline-block;
    z-index: 1;
    padding: auto 0;
    background: var(--green);
    background-image: url("../stockPhoto.png");
    
    
}

div.project_image_div:hover a{
    filter: opacity(0%);
}



div.project_image_div img {
    width: 100%;
    height: auto;
    position: absolute;
    
}



.project_image_link {
    width: 100%;
    height: 100%;
}


.project_summary {
    width:50%;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    max-height: 400px;
    text-align: center;
    padding: auto;
    z-index: 2;
}

div.project_description {
    color: var(--text);
    font-size: 1.5ch;
    padding: 20px;
    line-height: 2.5ch;
    background-color: var(--light-navy);
    
}

p.feature {
    font-size: 1.3ch;
    margin-bottom: 2%;
}

h2.projectName {
    margin-top: 0px;
    display: block;
}

ul.project_tech_list {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    list-style: none;
    padding: 0px;
}
.project_tech_list li {
    
    color: var(--text);
    font-size: 1.4ch;
    
}
div.feature_links {
    height: 40px;
    display: flex;
    
    
}

a.github_link{
    width: 30px;
    height: auto;
    margin: 5px;
}



a.github_link svg {
    object-fit: contain;
    
    
    font-family: var(--font-sans);
    font-size: var(--fz-xl);
    line-height: 1;
    
    text-align: right;
    
    color: var(--light-text);

    stroke: var(--light-text);
    stroke-width: 2;
    stroke-linecap: round;
    stroke-linejoin: round;
    max-width: 100%;
    vertical-align: middle;
    fill: none;
    width: 25px;
    height: 25px;
}

a:hover svg {
    stroke: var(--green);
}


@media (max-width: 1000px) {
    div.feature-project {
        max-width: 100%;
        width: 100%;
    }

    div.project-summary {
        max-width:inherit;
        width: 75%;
        position: absolute;
        visibility: show;
        float: left !important;
        text-align: left !important;
        display: flex;


    }

    div.project-image-div {
        width: auto;
        height: inherit;
        max-height: inherit;
        max-width: inherit;
        position: absolute;
        overflow: hidden;
        align-items: center;
        
        z-index: 1;
        padding: auto;
        margin-bottom: 50vh;
        
        visibility: hidden;
    }


     div.project-image-div a {
        height: 400px;
        max-height: 400px;
        width: auto;
        max-width: inherit;
        
        padding: auto;
        
    } 

    div.project-img {
        height: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        max-height: 400px;
        max-width: inherit;
        
    }

    div.project-img img {
        max-width: 100%;
    }
    
    div.project-image-div.left {
        left: inherit;
        margin-left: inherit;
    }
    
    div.project-image-div.right {
        right: inherit;
        margin-right: inherit;
    }
    
    
    
    div.project-image-div img {
        max-width: inherit;
        height: auto;
    }
    
    a.project-image-link {
        width: auto;
        height: 100%;
    }

    .project-summary h2 {
        text-align: left !important;
    }

    .project-summary ul.project-tech-list{
        justify-content: left !important;
        
    }
    .project-tech-list li {
        margin: 0px 25px 0px 0px !important;
        
    }

    .project-summary div.feature_links {
        justify-content: left !important;
    }

    .feature {
        width: auto;
    }

    .projectName {
        width: auto;
        height: auto;
    }

    div.project-description {
        width: auto;
    }

    div.project-description p {
        width: auto;
    }
}

