div#otherProjects {
    text-align: center;
    width: 100%;
}

div#otherProjects h2#other_project_header {
    align-self: center;
    display: block;
}

ul.projectContainer {
    display: grid;
    padding: 0px;
    position: relative;
    box-sizing: border-box;
    grid-template-columns:repeat(auto-fill, minmax(300px, 1fr));
    column-gap: 1.5%;
    row-gap: 15px;
    list-style: none;
    
}

ul.projectContainer li.otherProject {
    width: 100%;
    height: 100%;
    box-sizing: inherit;
    position: relative;
    display: list-item;
    background-color: var(--light-navy);
    border-radius: 1.5%;
    text-align: left;
}

ul.projectContainer li div.projectInner {
    padding: 2rem 1.75rem;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
    min-height: 100%;
    overflow: auto;
    position: relative;
    color: var(--light-text);
    box-sizing: inherit;
}

div.projectInner div.head div.project_header  {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 25px;
    width: 100%;
    
    
}
div.project_header div {
    height: 40px;
    width: 40px;
}

div.project_header svg {
    
    object-fit: contain;
    
    
    font-family: var(--font-sans);
    font-size: var(--fz-xl);
    line-height: 1;
    
    text-align: right;
    
    color: var(--light-text);

    stroke: var(--light-text);
    stroke-width: 1.5;
    stroke-linecap: round;
    stroke-linejoin: round;
    max-width: 100%;
    vertical-align: middle;
    fill: none;
    width: 25px;
    height: 25px;
}

div.project_header svg:hover {
    stroke: var(--green);
}

div.projectInner div.head h3 {
    margin-top: 10px;
    margin-bottom: 5px;
    font-size: 1.5rem;
    width: 100%;
}

div.projectInner:hover div.head h3 {
    color: var(--green);
    
}

div.projectInner p {
    font-size: 1.5ch;
    line-height: 2.5ch;
    color: var(--text);
}

div.projectInner div.project_footer {
    
}

div.project_footer ul {
    display: flex;
    flex-wrap: wrap;
    
    
}

div.project_footer ul li {
    height: 25px;
    margin-right: 25px;
    font-size: 0.75rem;
    color: var(--text);
}

ul.project_tech_list {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    list-style: none;
    padding: 0px;
}
.project_tech_list li {
    
    color: var(--text);
    font-size: 1.4ch;
    
}

a.card_link {
    display: block;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

