#contact_overlay {
    min-height: 100vh;
    max-width: 1600px;
    
    margin: 0 auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 0 150px;
    text-align: center;
    font-size: 3rem;
    
}
form {
    display: flex;
    flex-direction: column;
    width: 800px;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin: 0;
}

div#contact_form {
    border: none;
    width: 100%;
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: center;
    

}


form div {
    justify-content: center;
    text-align: center;
    width: 100%;
    margin-bottom: 25px;
    

}

form div#personal_info {

}

form div#personal_info input{
    display: inline-block;
    width: auto;
    margin: 0 20px;
    padding: 0;
    width: 230px;

    
}


form div:not(#personal_info) input,form div:not(#personal_info) textarea{
    margin: auto;
    padding: 0;
    width: 500px;
    

    
}

input,textarea {
    outline: 0;
    background: none;
    border: 0;
    
    border-bottom: 2px solid var(--green);
    display: block;
    font-size: 1.25rem;
    height: 2.5rem;
    color: var(--text);
    
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  
  -webkit-text-fill-color: var(--green);
  -webkit-box-shadow: 0 0 0px 1000px none inset;
  transition: background-color 5000s ease-in-out 0s;
}

input::placeholder, textarea::placeholder {
    font-family: "SF Mono","Fira Code","Fira Mono","Roboto Mono",Monospace;
}